import { defineDocumentType, makeSource } from 'contentlayer/source-files'
import path from 'path'

interface DocumentWithRaw {
  _raw: {
    flattenedPath: string
  }
}

export const Entry = defineDocumentType(() => ({
  name: 'Entry',
  filePathPattern: 'entries/**/*.mdx',
  contentType: 'mdx',
  fields: {
    title: { type: 'string', required: true },
    date: { type: 'date', required: true },
    tags: { type: 'list', of: { type: 'string' }, default: [] },
    type: { 
      type: 'enum', 
      options: ['note', 'journal', 'document'],
      required: true,
      default: 'document'
    },
    draft: { type: 'boolean' },
    summary: { type: 'string' },
  },
  computedFields: {
    path: {
      type: 'string',
      resolve: (doc: DocumentWithRaw) => `/${doc._raw.flattenedPath}`,
    },
    slug: {
      type: 'string',
      resolve: (doc: DocumentWithRaw) => path.basename(doc._raw.flattenedPath),
    },
  },
}))

export default makeSource({
  contentDirPath: 'data',
  documentTypes: [Entry],
})
